<template>
	<div class="gallery-wrapper" :style="{animation: closed ? 'opacityReverse 0.3s both' : 'opacity 0.3s both'}">
		<div class="image-wrapper">
			<div
				id="image"
				class="image"
				v-bg.contain="images[activeIndex].image"
			/>
			<div class="inner-images">
				<img
					:class="['inner-image', {scale: index === activeIndex}]"
					v-for="(image, index) in images.slice(0, 10)"
					:key="index+1"
					:src="image.image"
					alt=""
					@click="$emit('index-changed', -activeIndex + index)"
				/>
			</div>
		</div>
		<div>
			<div class="arrow" @click="changeImage('-')">
				<fa-icon icon="angle-left" />
			</div>
			<div class="arrow" @click="changeImage('+')">
				<fa-icon icon="angle-right" />
			</div>
		</div>
		<div>
			<div class="part" @click="changeImage('-')" />
			<div class="part" @click="changeImage('+')" />
		</div>
		<div class="exit" @click="close">
			<fa-icon icon="times" />
		</div>
	</div>
</template>

<script>
	export default {
		name: "Gallery",
		props: {
			images: Array,
			activeIndex: Number
		},
		data() {
			return {
				closed: false
			}
		},
		mounted() {
			document.addEventListener('keyup', (e) => {
				if (e.keyCode === 27) {
					this.close()
				}
				else if (e.keyCode === 39) {
					if (e.ctrlKey) {
						this.$emit('index-changed', -this.activeIndex + this.images.length - 1)
					}
					else {
						this.changeImage('+')
					}
				}
				else if (e.keyCode === 37) {
					if (e.ctrlKey) {
						this.$emit('index-changed', -this.activeIndex)
					}
					else {
						this.changeImage('-')
					}
				}
			})
		},
		methods: {
			changeImage(sign) {
				if (sign === '-') {
					this.activeIndex === 0 ? this.$emit('index-changed', this.images.length - 1) : this.$emit('index-changed', -1)
				}
				else if (sign === '+') {
					this.activeIndex === this.images.length - 1 ? this.$emit('index-changed', -(this.images.length - 1)) : this.$emit('index-changed', 1)
				}
			},
			close() {
				this.closed = true
				setTimeout(() => {
					this.$emit('exit')
					this.closed = false
				}, 300)
			}
		}
	}
</script>

<style lang="scss" scoped>
	.gallery-wrapper {
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: rgb($black, 0.9);
		z-index: 2;
		.image-wrapper {
			position: absolute;
			width: 100%;
			height: 100%;
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			.image {
				width: 100%;
				height:	100%;
				background: transparent center center no-repeat;
				background-size: contain;
				transition: background-image 0.3s 0.1s ease;
				@media (min-width: 800px) {
					margin-bottom: 20px;
					height: calc(100% - 130px);
				}
			}
			.inner-images {
				display: none;
				@media (min-width: 800px) {
					display: flex;
					justify-content: center;
					align-items: center;
					height: 60px;
					.inner-image {
						transition: 0.2s ease;
						width: 50px;
						height: 50px;
						margin: 10px;
						border-radius: 10px;
						opacity: 0.5;
						cursor: pointer;
						@include hover {
							opacity: 1;
						}
					}
					.scale {
						width: 60px;
						height: 60px;
						opacity: 1;
					}
				}
			}
		}
		.part {
			position: absolute;
			top: 0;
			width: 40px;
			height: 100%;
			cursor: pointer;
			background-color: transparent;
			opacity: 0;
			transition: all 0.3s ease;
			-webkit-tap-highlight-color: transparent;
			@include hover {
				opacity: 1;
			}
			&:nth-child(1) {
				left: 0;
				background: linear-gradient(90deg, rgb($primary, 0.2) 0%, transparent 100%);
			}
			&:nth-child(2) {
				right: 0;
				background: linear-gradient(-90deg, rgb($primary, 0.2) 0%, transparent 100%);
			}
		}
		.arrow {
			position: absolute;
			top: 50%;
			transform: translateY(-50%);
			width: 30px;
			height: 60px;
			display: flex;
			justify-content: center;
			align-items: center;
			font-size: 27px;
			color: $primary;
			background-color: $bg;
			opacity: 0.5;
			-webkit-tap-highlight-color: transparent;
			&:nth-child(1) {
				left: 0;
				border-radius: 0 20px 20px 0;
				padding-right: 5px;
			}
			&:nth-child(2) {
				right: 0;
				border-radius: 20px 0 0 20px;
				padding-left: 5px;
			}
			&:active {
				transform: scale(0.99) translateY(-50%);
			}
			cursor: pointer;
			transition: all 0.1s ease;
			@media (min-width: 800px) {
				background-color: transparent;
				color: $secondary
			}
		}
		.exit {
			position: absolute;
			top: 0;
			right: 0;
			width: 40px;
			height: 40px;
			display: flex;
			justify-content: center;
			align-items: center;
			font-size: 20px;
			color: $primary;
			background-color: #fff;
			opacity: 0.5;
			border-bottom-left-radius: 20px;
			transition: all 0.2s ease;
			@include hover {
				transform: scale(1.1);
			}
			@media (min-width: 800px) {
				top: 5px;
				right: 5px;
				background-color: transparent;
				color: $secondary
			}
		}
	}
</style>