<template>
	<fragment>
		<Gallery
			v-if="gallery.images"
			:images="gallery.images"
			:activeIndex="gallery.index"
			@index-changed="changeIndex"
			@exit="closeGallery"
		/>
		<div class="view" :style="{animation: !$loaded ? 'bottomElation 0.5s 1s both' : 'bottomElation 0.5s both'}">
			<section>
				<h2 class="orders-title">Wycieczka po dworku</h2>
				<div class="orders">
					<Order
						title="Część 1"
						id="part1"
						:images="photos.part1"
						:primaryIndex="1"
						@photo-selected="selectPhoto"
					/>
					<Order
						title="Część 2"
						id="part2"
						:images="photos.part2"
						:primaryIndex="1"
						@photo-selected="selectPhoto"
					/>
					<Order
						title="Część 3"
						id="part3"
						:images="photos.part3"
						:primaryIndex="1"
						@photo-selected="selectPhoto"
					/>
					<Order
						title="Część 4"
						id="part4"
						:images="photos.part4"
						:primaryIndex="1"
						@photo-selected="selectPhoto"
					/>
					<Order
						title="Część 5"
						id="part5"
						:images="photos.part5"
						:primaryIndex="1"
						@photo-selected="selectPhoto"
					/>
					<Order
						title="Część 6"
						id="part6"
						:images="photos.part6"
						:primaryIndex="1"
						@photo-selected="selectPhoto"
					/>
					<Order
						title="Część 7"
						id="part7"
						:images="photos.part7"
						:primaryIndex="1"
						@photo-selected="selectPhoto"
					/>
					
				</div>
			</section>
		</div>
	</fragment>
</template>

<script>
	import Order from '@/Components/Realizations/Order'
	import Gallery from '@/Components/Realizations/Gallery'
	const importAll = (r) => {
		const images = []
		r.keys().map(r).forEach((img, i) => images.push({
			image: img,
			id: i % 10
		}))
		return images;
	}
	const allImages = importAll(require.context('@/Assets/Images/gallery', false, /\.jpg/));
	const part1 = allImages.slice(0, 10)
	const part2 = allImages.slice(10, 20)
	const part3 = allImages.slice(20, 30)
	const part4 = allImages.slice(30, 40)
	const part5 = allImages.slice(40, 50)
	const part6 = allImages.slice(50, 60)
	const part7 = allImages.slice(60, 70)

	export default {
		name: "Realizations",
		components: {
			Order,
			Gallery
		},
		data() {
			return {
				photos: {
					allImages,
					part1,
					part2,
					part3,
					part4,
					part5,
					part6,
					part7,
				},
				gallery: {}
			}
		},
		methods: {
			changeIndex(number) {
				this.gallery.index += number
			},
			closeGallery() {
				this.gallery = {}
			},
			selectPhoto(id, index) {
				this.gallery = {
					images: this.photos[id],
					index
				}
			}
		}
	}
</script>

<style lang="scss" scoped>
	.orders-title {
		position: relative;
		display: inline-block;
		margin: 30px 0 ;
		font-size: 30px;
		@include line;
		/* color: $secondary; */
		&::first-letter {
			color: $decorative;
			font-size: 40px;
		}
	}
	.orders {
		display: flex;
		justify-content: center;
		flex-wrap: wrap;
	}
</style>